.footer{
  text-align: center;
  justify-self: center;
  align-self: center;
  min-height: 200px;
  background-color: black;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding-top: 20px;
  width: 100%;
}
.location p{
  color: white;
}
.contacts p{
  color: white;
}
.socialNet p{
  color: white;
}
.title{
  color: white;
  font-size: 20px;
}
.info{
  color: white;
  margin: 0;
}
.info p{
  color: white;
  margin: 5px;
}
.copyright{
  border-top: 1px solid grey ;
  color: white;
  text-align: center;
  justify-content: center;
  grid-column-start: 1;
  grid-column-end: 4;
  
}
.copyright p{
  margin-top: 6px;
  margin-bottom: 0;
}
