.company{
  background-color: black;
  text-align: center;
  justify-self: center;
  align-self: center;
  height: 700px;
}
.companyTitle h2{
  font-size: 2rem;
  color: white;
}
.description p{
  color: white;
  margin: 20px;
}
.aboutCompany{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.photoCompany{
  background-color: wheat;
  margin: 20px;
}